<template>
  <div class="container">
    <navbar>
      <p slot="title">动态详情</p>
    </navbar>
    <div class="content" id="content">
      <div class="top">
        <div class="top-left" @click="toUser">
          <div class="avatar">
            <img
              src="../../assets/login/q_message.svg"
              width="40"
              height="40"
              style="border-radius: 50%"
            />
          </div>
          <div class="name">
            <p>{{ row.name }}</p>
            <p>{{ row.address }}</p>
          </div>
        </div>
        <div class="btn" @click="attention">
          {{ row.isAtt ? "取消关注" : "关注" }}
        </div>
      </div>
      <div>
        <div class="text">
          {{ row.text }}
        </div>
        <div class="images" v-if="row.images">
          <van-grid :gutter="10" :column-num="3">
            <van-grid-item v-for="value in row.images" :key="value.url" />
          </van-grid>
        </div>
        <!-- <div class="video" v-if="row.video"></div> -->
        <video
          v-if="row.video"
          id="myVideo"
          :poster="row.posterImg"
          class="video-js vjs-default-skin vjs-big-play-centered video"
        >
          <source :src="row.video" type="video/mp4" />
        </video>
      </div>
      <div class="count">
        <div class="count-left">
          <div class="count-left-item">
            <van-icon name="good-job-o" /><span>168</span>
          </div>
          <div class="count-left-item">
            <van-icon name="comment-circle-o" /><span>8,668</span>
          </div>
          <div class="count-left-item">
            <van-icon name="share-o" /><span>356</span>
          </div>
        </div>
        <div>
          <van-icon name="weapp-nav" />
        </div>
      </div>
    </div>
    <div class="line"></div>
    <div class="comment" :style="myStyle">
      <div
        class="comment-item"
        v-for="(item, index) in commentList"
        :key="index"
      >
        <div class="info">
          <img :src="item.src" alt="" width="44" height="44" />
          <div class="base">
            <p>{{ item.nickname }}</p>
            <p>{{ item.time }}</p>
          </div>
        </div>
        <div class="message">
          {{ item.message }}
        </div>
      </div>
    </div>
    <div class="inputComment">
      <van-field v-model="comment" clearable placeholder="说点什么吧...">
        <template #extra>
          <van-icon name="smile-o" />
          <van-icon name="volume-o" />
        </template>
      </van-field>
    </div>
  </div>
</template>

<script>
import Video from "video.js";
import "video.js/dist/video-js.css";
import avatar from "../../assets/mine/avatar.svg";
import { Toast } from "vant";

export default {
  data() {
    return {
      contentHeight: "",
      myPlayer: "",
      comment: "",
      row: {
        // id: "",
        // name: "",
        // address: "",
        // text: "",
        // images: [],
      },
      commentList: [
        {
          src: avatar,
          nickname: "李辉",
          time: "24 分钟前",
          message: "你真的好可爱！！",
        },
        {
          src: avatar,
          nickname: "李辉",
          time: "24 分钟前",
          message: "你真的好可爱！！",
        },
        {
          src: avatar,
          nickname: "李辉",
          time: "24 分钟前",
          message: "你真的好可爱！！",
        },
        {
          src: avatar,
          nickname: "李辉",
          time: "24 分钟前",
          message: "你真的好可爱！！",
        },
        {
          src: avatar,
          nickname: "李辉",
          time: "24 分钟前",
          message: "你真的好可爱！！",
        },
        {
          src: avatar,
          nickname: "李辉",
          time: "24 分钟前",
          message: "你真的好可爱！！",
        },
      ],
    };
  },
  computed: {
    myStyle() {
      return {
        "--myStyle": this.contentHeight ? this.contentHeight : 0,
      };
    },
    // contentHeight() {
    //   this.$nextTick(() => {
    //     // alert(document.getElementById("content").clientHeight);
    //     return document.getElementById("content").clientHeight || 0;
    //   });
    // },
  },
  mounted() {
    this.row = this.$route.params;
    // 只有一个视频时;
    setTimeout(() => {
      if (this.row.video) {
        this.myPlayer = Video(
          "myVideo",
          {
            controls: true, //显示底部控件
            muted: true, // 静音播放
            autoplay: false, // 如果true,浏览器准备好时开始回放。
            playsinline: true,
            preload: "auto", // 建议浏览器是否应在<video>加载元素后立即开始下载视频数据
          },
          function () {
            // 事件
          }
        );
      }
      this.contentHeight = document.getElementById("content").clientHeight;
    }, 500);
  },
  methods: {
    toUser() {
      this.$router.push("/user");
    },
    attention() {
      this.row.isAtt = !this.row.isAtt;
      if (this.row.isAtt) {
        Toast.success("已关注");
      } else {
        Toast.success("取消关注成功");
      }
    },
  },
  beforeDestroy() {
    if (this.myPlayer) {
      this.myPlayer.dispose();
    }
  },
};
</script>

<style scoped>
.container {
  /* background-color: #fff; */
  height: calc(100vh - 50px);
}
.top,
.count {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.top-left,
.count-left {
  display: flex;
  align-items: center;
}

.avatar {
  border-radius: 50%;
  width: 40px;
  height: 40px;
  background-color: #fff;
}

.name {
  margin-left: 5px;
}

.name p {
  margin: 0;
}

.name p:first-child {
  font-size: 13px;
  margin-top: 5px;
}

.name p:last-child,
.count-left-item span {
  font-size: 12px;
  color: #999;
}

.btn {
  width: 60px;
  height: 25px;
  line-height: 25px;
  background: linear-gradient(to bottom, #7a8dea, #6e86ff 100%) !important;
  border-radius: 3px;
  color: #fff;
  text-align: center;
  font-size: 12px;
}

.text {
  font-size: 13px;
  margin: 8px 0;
  color: #1d1d1f;
}

.images {
  width: 95vw;
  margin-left: -2.5vw;
}

.video {
  width: 90vw;
  height: 26vh;
  background-color: #999;
}

>>> .van-grid-item {
  height: 12vh;
}

>>> .van-grid-item__content {
  background: #999;
}

>>> .van-icon-good-job-o:before,
>>> .van-icon-comment-circle-o:before,
>>> .van-icon-share-o:before,
>>> .van-icon-weapp-nav:before {
  color: #999;
}

.count-left {
  margin-top: 8px;
  margin-bottom: 15px;
}

.count-left-item {
  margin-right: 30px;
}

.info {
  display: flex;
  align-items: center;
}

.info p {
  margin: 0;
}

.info p:first-child {
  font-family: "PingFang SC";
  font-style: normal;
  font-weight: 700;
  font-size: 15px;
  line-height: 18px;
  color: #1d1d1f;
}

.info p:last-child {
  font-family: "PingFang SC";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 16px;
  color: rgba(0, 0, 0, 0.4);
  margin-top: 2px;
}

.base {
  margin-left: 6px;
}

.message {
  font-family: "PingFang SC";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  display: flex;
  align-items: flex-end;
  color: rgba(0, 0, 0, 0.7);
  margin-left: 50px;
  margin-top: 5px;
}

.comment-item {
  border-bottom: 0.5px solid #e1e1e1;
  padding: 15px 0;
}

.comment {
  margin-top: 10px;
  height: calc(100vh - ((var(--myStyle) + 140) * 1px));
  /* height: 300px; */
  overflow: auto;
}
.comment::-webkit-scrollbar {
  display: none;
}

.line {
  background: #f9f9f9;
  height: 10px;
  width: 100vw;
  margin-left: -20px;
}
.inputComment {
  width: 100vw;
  height: 50px;
  position: fixed;
  bottom: 0;
  margin-left: -20px;
}

.inputComment .van-field {
  width: 90vw;
  margin-left: 5vw;
  /* margin-top: 10px; */
}

.inputComment .van-icon {
  font-size: 22px;
}

.inputComment .van-icon:last-child {
  margin-left: 6px;
}
>>> .van-field {
  background: #f9f9f9;
  border-radius: 8px;
}
</style>